<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">秒杀</a></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <div class="tool_group">
        <div class="btn_group">
          <router-link to="/super/market/bargains/add"><el-button type="success" size="small" icon="el-icon-plus" class="inquire" round>新增活动</el-button></router-link>
          <!-- <el-button type="danger" size="small" icon="el-icon-delete" round>批量删除</el-button> 后端没接口 -->
        </div>
        <el-form class="input_group" :model="form" :inline="true" size="mini">
          <el-form-item label="产品名称">
            <el-input v-model="form.secondsKillName" placeholder="产品名称" />
          </el-form-item>
          <el-form-item label="时间筛选 ">
            <el-date-picker v-model="form.startTime" type="date" placeholder="开始日期"></el-date-picker>至
            <el-date-picker v-model="form.endTime" type="date" placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width: 80px" @click="onSearch">查询</el-button>
          </el-form-item>
        </el-form>
      </div>

      <el-table :data="bargainsList" style="width: 100%" height="520">
        <el-table-column type="selection" width="55" align="center" />
        <el-table-column prop="secondsKillName" label="名称" align="center" />
        <el-table-column prop="secondsStartTime" label="活动时间" align="center" />
        <el-table-column label="活动状态" align="center">
          <template slot-scope="scope">
            <span v-if="new Date().getTime() < new Date(scope.row.secondsStartTime).getTime()">未开始</span>
            <span v-else-if="new Date().getTime() > new Date(scope.row.secondsStartTime).getTime() && new Date().getTime() < new Date(scope.row.secondsStopTime).getTime()">进行中</span>
            <span v-else-if="new Date().getTime() > new Date(scope.row.secondsStopTime).getTime()">已结束</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <!-- <el-button style="color: #5bd9be" type="text" @click="onUpdate(scope)">编辑</el-button> -->
            <el-button v-if="scope.row.state === 1" style="color: #fd9a30" type="text" @click="onEnd(scope)">终止</el-button>
            <el-button v-else-if="scope.row.state === 0" style="color: #fd9a30" type="text" @click="onStart(scope)">启用</el-button>
            <el-popconfirm
              confirm-button-text="好的"
              cancel-button-text="不用了"
              icon="el-icon-info"
              icon-color="red"
              title="这是一段内容确定删除吗？"
              @confirm="onDelete(scope)"
            >
              <el-button slot="reference" style="color: #ff474c" type="text">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="bargainsTotal" @current-change="handleCurrentChange" />
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'BargainsList',

  data() {
    return {
      form: {}, // 查询
      bargainsTotal: 0, // 秒杀分页总条数
      activeId: '', // 活动类型 0-团购 1-秒杀
      pageNo: 1, // 当前页
      bargainsList: [], // 秒杀
      activityData: {} // 活动详情
    }
  },

  created() {
    this.activeId = 1
    this.apiActivityList()
  },

  methods: {
    delData () {
      console.log(111)
    },
    cancelData () {
      console.log(222)
    },
    // 分页
    handleCurrentChange(pageNo) {
      this.pageNo = pageNo
      this.apiActivityList()
    },
    // 终止 0-上架 1-下架
    onEnd(scope) {
      this.apiActivityUpOrDown(scope.row.id, 0)
        .then((res) => {
          this.$message.success('已终止活动')
          scope.row.state = 0
        })
    },
    // 启用 0-上架 1-下架
    onStart(scope) {
      this.apiActivityUpOrDown(scope.row.id, 1)
        .then((res) => {
          this.$message.success('已启用活动')
          scope.row.state = 1
        })
    },
    // 删除
    onDelete(scope) {
      this.apiActivityDelete(scope)
    },
    // 编辑
    onUpdate(scope) {
      this.$router.push({ path: '/mark/list/bargainsList/addBargains', query: { id: scope.row.id }})
    },
    // 详情
    onContent(scope) {
      this.apiFindById(scope.row.id)
    },
    // 查询
    onSearch () {
      this.apiActivityList()
    },

    // api接口 ----------------------------
    // 获取秒杀列表
    apiActivityList() {
      let data = {
        pageNo: this.pageNo,
        pageSize: 10,
        activeType: this.activeId,
        // type: 1, // 商家的
      }
      Object.assign(data, this.form) // 模糊查询
      this.$http.post(this.$api.querySmallSeckill, data)
        .then((res) => {
          this.bargainsList = res.data.data.list
          console.log(res)
          this.bargainsTotal = res.data.data.total
          // 把时间转换成时间戳再在对象里面添加进去
          this.bargainsList.map((item) => {
            item.startTimeDate = new Date(item.startTime.replace(/-/g, '/')).getTime()
            item.endTimeDate = new Date(item.endTime.replace(/-/g, '/')).getTime()
          })
        })
    },
    // 0-上架 1-下架
    apiActivityUpOrDown(id, state) {
      return this.$http.post(`/woho/activity/manage/upOrDown/${id}/${state}`)
        .then((res) => {
          if (res.data.success) {
            return
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 删除活动
    apiActivityDelete(scope) {
      this.$http.get(`${this.$api.deleteSmallSeckill}/${scope.row.id} `)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('删除成功')
            this.bargainsList.splice(scope.$index, 1)
          }
        })
    }
    // api接口 ----------------------------
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    margin-top:15px;
    .tool_group{
      display: flex;
    }
    .input_group{
      margin-left: 30px;
    }
  }
</style>
